<template>
  <div>
    <!-- Modal -->
    <modal
      :popup="showModal"
      :title="modalTitle"
      :cancel="+modalType !== 1"
      v-bind="{
        ...(+modalType === 2
          ? { classBody, }
          : {})
      }"
      :confirm-text="+modalType === 2 ? '儲存' : '確定'"
      @finish="closeMod()"
      @confirm="onConfirm"
    >
      <p
        v-if="+modalType < 2"
        v-text="modalMessage"
      />
      <!-- 新增 Modal -->
      <div
        v-else
        class="modal-body"
      >
        <div
          v-for="item in inputList.basic"
          :key="item.title"
          class="form-group row"
        >
          <label
            for="lname"
            class="col-sm-2 text-right control-label col-form-label"
            v-text="item.title"
          />
          <div class="col-sm-10">
            <input
              v-model="item.value"
              type="text"
              class="form-control"
              :placeholder="item.placeholder"
            >
          </div>
        </div>
        <div class="form-group row">
          <template
            v-for="(item, index) in inputList.weight"
          >
            <label
              :key="item.title"
              :class="[index === 0 ? 'col-sm-2' : 'col-sm-1', 'text-right', 'control-label', 'col-form-label']"
              v-text="item.title"
            />
            <div
              :key="item.title + index"
              class="col-sm-2"
            >
              <input
                v-model="item.value"
                class="form-control"
                :placeholder="item.placeholder"
                :type="item.type"
              >
            </div>
          </template>
          <div class="col-sm-3">
            <select
              v-model="selectedRate.main"
              class="form-control"
            >
              <option
                v-for="({ CurrencyName, Rate, Id }, index) in exchangeList"
                :key="index"
                :value="Id"
                v-text="`${CurrencyName} (匯率 ${Rate})`"
              />
            </select>
          </div>
        </div>
        <div class="form-group row">
          <label
            class="col-sm-2 text-right control-label col-form-label"
            v-text="inputList.remark[0].title"
          />
          <div class="col-sm-10">
            <textarea
              v-model="inputList.remark[0].value"
              class="form-control"
              style="height:100px;"
              :placeholder="inputList.remark[0].placeholder"
            />
          </div>
        </div>
        <li
          v-if="alertMessage"
          style="color: #F00"
          v-text="alertMessage"
        />
        <hr>
        <div
          class="form-group row"
          style="text-align: left;"
        >
          <template
            v-for="(item, index) in inputList.weightSub"
          >
            <label
              v-if="item.title"
              :key="item.title"
              class="col-sm-1 text-right control-label col-form-label"
              v-text="item.title"
            />
            <input
              v-if="item.type"
              :key="index"
              v-model="item.value"
              :type="item.type"
              :class="['form-control', 'col-md-2', index !== 0 ? 'ml-1' : '']"
              :placeholder="item.placeholder"
            >
          </template>
          <div class="col-sm-2">
            <select
              v-model="selectedRate.sub"
              class="form-control"
            >
              <option
                v-for="({ CurrencyName, Rate, Id }, index) in exchangeList"
                :key="index"
                :value="Id"
                v-text="`${CurrencyName} (匯率 ${Rate})`"
              />
            </select>
          </div>
          <button
            class="btn btn-outline-danger col-md-1 align-self-end ml-1"
            @click="onModalAdd"
            v-text="'加入'"
          />
        </div>
        <div class="form-group row">
          <table class="table spec">
            <thead class="thead-light">
              <th
                v-for="(title, index) in ['重量', '運費', '運費匯率', '台幣運費', '操作']"
                :key="title+index"
                scope="col"
                style="width:180px;"
                v-text="title"
              />
            </thead>
            <tbody>
              <tr
                v-for="({ beginWeight, endWeight, fee, name, rate }, index) in modalFeeList"
                :key="index"
              >
                <td
                  class="vertical-align"
                  v-text="`${beginWeight} ~ ${endWeight} 公斤`"
                />
                <td
                  class="vertical-align"
                  v-text="fee"
                />
                <td
                  class="vertical-align"
                  v-text="`${name} (${rate})`"
                />
                <td
                  class="vertical-align"
                  v-text="fee*rate"
                />
                <td>
                  <button
                    v-if="index + 1 >= modalFeeList.length"
                    class="btn btn-danger"
                    style="width:85px;"
                    @click="modalFeeList.splice(index, 1)"
                    v-text="'刪除'"
                  />
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </modal>
    <!-- Modal -->

    <div class="page-wrapper">
      <div class="page-breadcrumb">
        <div class="row">
          <div class="col-12 d-flex no-block align-items-center">
            <h4 class="page-title">
              集運地點及運費
            </h4>
          </div>
        </div>
      </div>
      <div class="container-fluid">
        <buttonCont
          :style-data="styleData"
          @onButton="callModal($event)"
        />
        <data-table
          :pagination="pagination"
          @onPage="onPage"
          @check="onAllCheck"
        >
          <template slot="head">
            <th
              v-for="(title, index) in [ '集運商名稱', '發貨方式及地點', '首重']"
              :key="title+index"
              style="min-width:150px;"
              class="text-center py-4"
              v-text="title"
            />
            <th
              class="head-th p-0"
            >
              <table
                class="text-center w-100"
                style="min-width:550px;"
              >
                <tr>
                  <td
                    v-for="(title, index) in [ '重量', '運費', '運費匯率', '台幣運費']"
                    :key="title+index"
                    style="width:25%;"
                    class="py-4"
                    v-text="title"
                  />
                </tr>
              </table>
            </th>
            <th
              style="min-width:200px;"
              class="text-center py-4"
              v-text="'其他資訊'"
            />
          </template>
          <tr
            v-for="({ Id, Name, DeliveryLocation, ContactPerson, PhoneNumber, Address, FirstKG, FirstFee, ExchangeRateId, Remark, Status, TransFeeData }, key) in list"
            :key="key"
          >
            <td>
              <label class="customcheckbox m-b-20">
                <input
                  ref="checkbox"
                  type="checkbox"
                  @input="onCheck({ $event, Id, key })"
                >
                <span class="checkmark" />
              </label>
            </td>
            <td>
              <li>
                <a
                  href="javascript:void(0)"
                  class="product-spec"
                  v-text="Name"
                />
              </li>
              <li
                v-if="Status"
                style="color: #009688"
              >
                正常使用 <i class="m-r-10 mdi mdi-check" />
              </li>
              <li
                v-else
                style="color: #acacac"
              >
                停用
              </li>
            </td>
            <td v-text="DeliveryLocation" />
            <td>
              <li v-text="`首重 ${+FirstKG} 公斤`" />
              <li v-text="`首重費： ${+FirstFee}`" />
              <li
                v-for="{CurrencyName, Rate, Id} in exchangeList.filter(({Id})=> Id === ExchangeRateId)"
                :key="Id"
                v-text="`幣別：${CurrencyName}(${Rate})`"
              />
            </td>
            <td class="p-0">
              <table
                class="table-spec"
                style="min-width:550px !important;width:100%;"
              >
                <tbody>
                  <tr
                    v-for="({ BeginWeight, EndWeight, ExchangeRateId, Id, NTPrice, Price }, index) in TransFeeData"
                    :key="index"
                  >
                    <td
                      style="width:25%"
                      v-text="`${BeginWeight} ~ ${EndWeight}公斤`"
                    />
                    <td
                      style="width:25%"
                      v-text="+Price"
                    />
                    <td style="width:25%">
                      <span
                        v-for="{CurrencyName, Rate, Id} in exchangeList.filter(({Id})=> Id === ExchangeRateId)"
                        :key="Id"
                        v-text="`${CurrencyName}(${Rate})`"
                      />
                    </td>
                    <td
                      style="width:25%"
                      v-text="+NTPrice"
                    />
                  </tr>
                </tbody>
              </table>
            </td>
            <td>
              <dropdown
                :title="'操作'"
                :items="[
                  { text: '修改', type: 'item', action: 'edit' },
                  { type: 'divider' },
                  { text: Status ? '停止使用' : '啟用', type: 'item', action: 'toggle' },
                  { text: '刪除', type: 'item', action: 'delete' },
                ]"
                @onMenu="onMenu({ ...$event, Id, Status, key, })"
              />
              <hr>
              備註：<br>{{ Remark || '(無備註)' }}
            </td>
          </tr>
        </data-table>
      </div>
    </div>
    <div
      v-if="showModal"
      class="modal-backdrop fade show"
    />
  </div>
</template>

<script>
import dataTable from '@/components/dataTable.vue';
import dropdown from '@/components/dropdown.vue';
import modal from '@/components/modal/index.js';
// import addLocation from '@/components/addLocation.vue';
import buttonCont from '@/components/buttonCont.vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'Translocation',
  components: {
    dataTable,
    dropdown,
    modal,
    buttonCont,
    // addLocation,
  },
  data() {
    return {
      modifyingId: '',
      // -- modal --
      showModal: false,
      modalTitle: '',
      modalMessage: '',
      modalType: 0, // 1: 只有確認 2: 填單
      // -- modal --
      alertMessage: '',
      classBody: {
        'modal-dialog': true,
        'modal-900': true,
      },
      checkedList: [],
      inputList: {
        basic: [
          { title: '*集運商名稱', placeholder: '請輸入', value: '', type: 'input' },
          { title: '發貨地點及方式', placeholder: '例如：義烏海快普貨 或 深圳空運特貨', value: '', type: 'input' },
          { title: '收貨連絡人', placeholder: '請輸入', value: '', type: 'input' },
          { title: '收貨電話', placeholder: '請輸入', value: '', type: 'input' },
          { title: '收貨地址', placeholder: '請輸入', value: '', type: 'input' },
        ],
        weight: [
          { title: '首重公斤數', placeholder: '請輸入', value: '', type: 'number' },
          { title: '首重費', placeholder: '請輸入', value: '', type: 'number' },
        ],
        weightSub: [
          { title: '', placeholder: '*公斤', value: '', type: 'number' },
          { title: '～', placeholder: '*公斤', value: '', type: '' },
          { title: '', placeholder: '*公斤', value: '', type: 'number' },
          { title: '運費', placeholder: '*運費', value: '', type: 'number' },
        ],
        remark: [
          { title: '備註', placeholder: '', value: '', type: 'textarea' },
        ],
      },
      selectedRate: {
        main: 0,
        sub: 0,
      },
      modalFeeList: [],
      // -- Buttons --
      styleData: [
        {
          forBut: { color: 1, mdi: 1, text: '新增集運地點' },
          forMod: { title: '新增集運地點', type: '2', message: '1', action: 'onCreate' },
        },
        {
          forBut: { color: 2, mdi: 2, text: '正常使用' },
          forMod: { title: '', type: '', message: 2, action: 'onEnableAll' },
        },
        {
          forBut: { color: 2, mdi: 3, text: '停止使用' },
          forMod: { title: '', type: '', message: 3, action: 'onStopAll' },
        },
        {
          forBut: { color: 3, mdi: 4, text: '刪除' },
          forMod: { title: '', type: '', message: 4, action: 'onDeleteAll' },
        },
      ],
    };
  },
  computed: {
    ...mapGetters({
      exchangeList: 'ExchangeRate/list',
      list: 'TransLocation/list',
      pagination: 'TransLocation/pagination',
    }),
  },
  created() {},
  mounted() {
    console.log('debug: mounted -> window.tr = this', window.tr = this);
    this.getList({
      ...this.pagination,
      page: 1,
      PageLimit: 10,
    });
    this.getExchangeList({
      page: 1,
      PageLimit: 50,
    });
  },
  methods: {
    ...mapActions('ExchangeRate', {
      getExchangeList: 'read',
    }),
    ...mapActions('TransLocation', {
      getList: 'read',
      addLocation: 'create',
      changeSetting: 'modify',
      changeStatus: 'update',
      deleteLocation: 'deleted',
    }),
    ...mapActions('Modal', {
      createModal: 'create',
    }),
    callModal({ title, type, message, action }) {
      if (['onCreate', 'onEdit'].includes(action)) {
        document.body.classList.add('modal-open');
        document.body.style.paddingRight = '17px';
        this.showModal = true;
        this.modalTitle = title || '提示訊息';
        this.modalMessage = message || '';
        this.modalType = type || 0;
        this.action = action || 'closeMod';
        this.alertMessage = '';
      } else if (typeof this[action] === 'function') {
        this[action]();
        // onEnableAll
        // onStopAll
        // onDeleteAll
      }
    },
    onModalAdd() {
      const { modalFeeList } = this;
      const last = modalFeeList[modalFeeList.length - 1];
      const { weightSub } = this.inputList;
      const {
        0: { value: beginWeight },
        2: { value: endWeight },
        3: { value: fee },
      } = weightSub;
      const rateId = this.selectedRate.sub;
      const { CurrencyName: name, Rate: rate } = this.exchangeList.filter(({ Id }) => Id === rateId)[0] || {};
      // [條件, 錯誤訊息]
      const validList = [
        [!beginWeight && beginWeight !== 0, '請輸入最低重量'],
        [!endWeight, '請輸入最高重量'],
        [endWeight < beginWeight, '最高重量不可小於最低重量'],
        [!fee && fee !== 0, '請輸入運費'],
        [!rateId || !name || !rate, '請選擇匯率'],
        [last && +last.endWeight !== +beginWeight, '需接續上一筆資料重量'],
      ];
      let valid = true;
      // eslint-disable-next-line no-restricted-syntax
      for (const term of validList) {
        if (term[0]) {
          this.alertMessage = term['1'];
          valid = false;
          break;
        }
      }
      if (!valid) {
        return;
      }
      modalFeeList.push({ beginWeight, endWeight, fee, name, rate, rateId });
    },
    onConfirm() {
      const action = this[this.action];
      console.log('debug: onConfirm -> action', action);
      if (typeof action === 'function') {
        action();
      }
    },
    closeMod() {
      document.body.classList.remove('modal-open');
      document.body.style.paddingRight = null;
      this.showModal = false;
      this.modalMessage = '';
      this.alertMessage = '';
      Object.keys(this.inputList).forEach((key) => {
        this.inputList[key].forEach((input) => {
          input.value = '';
        });
      });
      this.modalFeeList.splice(0);
    },
    onCreate() {
      const { inputList, selectedRate, modalFeeList } = this;
      const { basic, weight, remark } = inputList;

      const {
        0: { value: Name },
        1: { value: DeliveryLocation },
        2: { value: ContactPerson },
        3: { value: PhoneNumber },
        4: { value: Address },
      } = basic;

      const {
        0: { value: FirstKG },
        1: { value: FirstFee },
      } = weight;

      const [{ value: Remark }] = remark;

      const ExchangeRateId = selectedRate.main;

      const TransFeeData = modalFeeList.map((item) => {
        const { beginWeight, endWeight, fee, rate, rateId } = item;
        return {
          Id: rateId,
          BeginWeight: beginWeight,
          EndWeight: endWeight,
          Price: fee,
          NTPrice: fee * rate,
          ExchangeRateId: rateId,
        };
      });

      if (Name && ExchangeRateId) {
        // if (true) {
        this.addLocation({
          Name,
          DeliveryLocation,
          ContactPerson,
          PhoneNumber,
          Address,
          FirstKG,
          FirstFee,
          ExchangeRateId,
          TransFeeData,
          Status: true,
          Remark,
        })
          .then((result) => {
            this.closeMod();
            this.createModal({
              message: '新增成功',
              showCancel: false,
            }).then(() => {});
            // this.modalMessage = '新增成功';
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            this.alertMessage = err.message || '不明錯誤';
          });
      } else {
        this.alertMessage = '所有星號(*)及匯率必填';
      }
    },
    onEdit() {
      const { inputList, selectedRate, modalFeeList } = this;
      const { basic, weight, remark } = inputList;

      const {
        0: { value: Name },
        1: { value: DeliveryLocation },
        2: { value: ContactPerson },
        3: { value: PhoneNumber },
        4: { value: Address },
      } = basic;

      const {
        0: { value: FirstKG },
        1: { value: FirstFee },
      } = weight;

      const [{ value: Remark }] = remark;

      const ExchangeRateId = selectedRate.main;

      const TransFeeData = modalFeeList.map((item) => {
        const { beginWeight, endWeight, fee, rate, rateId } = item;
        return {
          // Id: rateId,
          BeginWeight: beginWeight,
          EndWeight: endWeight,
          Price: fee,
          NTPrice: fee * rate,
          ExchangeRateId: rateId,
        };
      });

      if (Name && ExchangeRateId) {
        // if (true) {
        this.changeSetting({
          Id: this.modifyingId,
          data: {
            Name,
            DeliveryLocation,
            ContactPerson,
            PhoneNumber,
            Address,
            FirstKG,
            FirstFee,
            ExchangeRateId,
            TransFeeData,
            Status: true,
            Remark,
          } })
          .then((result) => {
            this.closeMod();
            this.createModal({
              message: '修改成功',
              showCancel: false,
            }).then(() => {});
            // this.modalMessage = '新增成功';
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            this.alertMessage = err.message || '不明錯誤';
          });
      } else {
        this.alertMessage = '所有星號(*)必填';
      }
    },
    onMenu({ text, type, index, action, Id, Status, key }) {
      if (action === 'edit') {
        const { Id, Name, DeliveryLocation, ContactPerson, PhoneNumber, Address, FirstKG, FirstFee, ExchangeRateId, Remark, Status, TransFeeData } = this.list[key];

        const { inputList, selectedRate, modalFeeList } = this;

        const { basic, weight, remark } = inputList;

        [Name, DeliveryLocation, ContactPerson, PhoneNumber, Address].forEach((val, index) => {
          basic[index].value = val;
        });

        [FirstKG, FirstFee].forEach((val, index) => {
          weight[index].value = val;
        });

        remark[0].value = Remark;
        selectedRate.main = ExchangeRateId;

        modalFeeList.splice(0);
        TransFeeData.forEach((item) => {
          // console.log(`debug: onMenu -> item`, item)
          const { BeginWeight, EndWeight, Price, NTPrice, ExchangeRateId } = item;
          const rateData = this.exchangeList.filter(({ Id }) => Id === ExchangeRateId)[0] || {};
          const { CurrencyName, Rate } = rateData;
          modalFeeList.push({
            beginWeight: BeginWeight,
            endWeight: EndWeight,
            rate: Rate,
            name: CurrencyName,
            fee: Price,
            rateId: ExchangeRateId,
          });
        });

        this.callModal({
          message: '',
          title: '修改集運地點資料',
          action: 'onEdit',
          type: 2,
        });
        this.modifyingId = Id;
      } else if (action === 'delete') {
        // 下拉選單刪除
        this.createModal({
          message: '確定刪除此項目？',
          // showCancel: false
        }).then(() => {
          this.deleteLocation(Id)
            .then((result) => {
              this.createModal({
                message: '刪除成功',
                showCancel: false,
              }).then(() => {});
              this.getList({
                ...this.pagination,
              });
            })
            .catch((err) => {
              this.createModal({
                message: err.message || '不明錯誤',
                showCancel: false,
              }).then(() => {});
            });
        });
        // 下拉選單刪除
      } else if (action === 'toggle') {
        // 下拉選單 停用/啟用
        this.createModal({
          message: `確定${Status ? '停用' : '啟用'}此項目？`,
          // showCancel: false
        }).then(() => {
          this.changeStatus({
            Id,
            Status: !Status,
          })
            .then((result) => {
              this.createModal({
                message: '修改成功',
                showCancel: false,
              }).then(() => {});
              this.getList({
                ...this.pagination,
              });
            })
            .catch((err) => {
              this.createModal({
                message: err.message || '不明錯誤',
                showCancel: false,
              }).then(() => {});
            });
        });
        // 下拉選單 停用/啟用
      }
    },
    onCheck({ $event, Id, key }) {
      const { checked } = $event.target;
      console.log('debug: onCheck -> checked', checked);
      if (checked && !this.checkedList.includes(Id)) {
        this.checkedList.push(Id);
      }
      if (!checked && this.checkedList.includes(Id)) {
        const index = this.checkedList.indexOf(Id);
        // console.log('debug: onCheck -> index', index);
        if (index > -1) {
          this.checkedList.splice(index, 1);
        }
      }
    },
    onAllCheck(checked) {
      if (checked) {
        const { checkbox } = this.$refs;
        const list = Array.isArray(checkbox)
          ? checkbox
          : [checkbox];
        list.forEach((el) => {
          el.checked = true;
        });
        this.checkedList.splice(0);
        this.list.forEach(({ Id }) => {
          this.checkedList.push(Id);
        });
      } else {
        this.clearAllchecked();
      }
    },
    clearAllchecked() {
      const { checkbox } = this.$refs;
      const list = Array.isArray(checkbox)
        ? checkbox
        : [checkbox];
      list.forEach((el) => {
        el.checked = false;
      });
      this.checkedList.splice(0);
    },
    onEnableAll() {
      if (this.checkedList.length) {
        this.createModal({
          message: '確定啟用全部勾選的項目？',
          // showCancel: false
        }).then(() => {
          const promiseList = [];
          this.checkedList.forEach((Id) => {
            promiseList.push(this.changeStatus({
              Id,
              Status: true,
            }));
          });
          Promise.allSettled(promiseList)
            .then((result) => {
              // this.closeMod();
              this.createModal({
                message: '修改成功',
                showCancel: false,
              }).then(() => {});
              // this.modalMessage = '修改成功';
              this.getList({
                ...this.pagination,
              });
            })
            .catch((err) => {
              this.createModal({
                message: err.message || '不明錯誤',
                showCancel: false,
              }).then(() => {});
              // this.modalMessage = err.message || '不明錯誤';
            });
        });
      } else {
        this.createModal({
          message: '請先勾選要設定的項目',
          showCancel: false,
        }).then(() => {});
      }
    },
    onStopAll() {
      if (this.checkedList.length) {
        this.createModal({
          message: '確定停用全部勾選的項目？',
          // showCancel: false
        }).then(() => {
          const promiseList = [];
          this.checkedList.forEach((Id) => {
            promiseList.push(this.changeStatus({
              Id,
              Status: false,
            }));
          });
          Promise.allSettled(promiseList)
            .then((result) => {
              // this.clearAllchecked();
              // this.closeMod();
              this.createModal({
                message: '修改成功',
                showCancel: false,
              }).then(() => {});
              // this.modalMessage = '修改成功';
              this.getList({
                ...this.pagination,
              });
            })
            .catch((err) => {
              // this.clearAllchecked();
              this.createModal({
                message: err.message || '不明錯誤',
                showCancel: false,
              }).then(() => {});
              // this.modalMessage = err.message || '不明錯誤';
            });
        });
      } else {
        this.createModal({
          message: '請先勾選要設定的項目',
          showCancel: false,
        }).then(() => {});
      }
    },
    onDeleteAll() {
      if (this.checkedList.length) {
        this.createModal({
          message: '確定刪除全部勾選的項目？',
          // showCancel: false
        }).then(() => {
          const promiseList = [];
          this.checkedList.forEach((Id) => {
            promiseList.push(this.deleteLocation(Id));
          });
          Promise.allSettled(promiseList)
            .then((result) => {
              this.clearAllchecked();
              // this.closeMod();
              this.createModal({
                message: '刪除成功',
                showCancel: false,
              }).then(() => {});
              // this.modalMessage = '修改成功';
              this.getList({
                ...this.pagination,
              });
            })
            .catch((err) => {
              this.clearAllchecked();
              this.createModal({
                message: err.message || '不明錯誤',
                showCancel: false,
              }).then(() => {});
              // this.modalMessage = err.message || '不明錯誤';
            });
        });
      } else {
        this.createModal({
          message: '請先勾選要設定的項目',
          showCancel: false,
        }).then(() => {});
      }
    },
    onPage(Page) {
      if (+Page !== +this.pagination.Page) {
        this.getList({
          ...this.pagination,
          Page,
        });
        this.clearAllchecked();
      }
    },
  },
};

</script>

